@import url(https://maxcdn.bootstrapcdn.com/bootstrap/latest/css/bootstrap.min.css);
.App {
  font-family: sans-serif;
  text-align: center;
}

:root {
  --red: #d20a0a;
}

body p {
  color: gray;
  font-size: 18px;
}

a {
  color: #d20a0a;
  color: var(--red);
}

.bg-red {
  background: #d20a0a;
  background: var(--red);
}

.bg-black {
  background: black;
}

.text-white {
  color: white;
}

/* containers */

.container-xs {
  max-width: 540px !important;
}

.bottom-border {
  border-bottom: #d20a0a 5px solid;
  border-bottom: var(--red) 5px solid;
}

/* buttons */

.btn-primary {
  color: #fff;
  background-color: #d20a0a;
  background-color: var(--red);
  border-color: #d20a0a;
  border-color: var(--red);
}

/* footer */

.footer p {
  font-size: 14px;
}

