:root {
  --red: #d20a0a;
}

body p {
  color: gray;
  font-size: 18px;
}

a {
  color: var(--red);
}

.bg-red {
  background: var(--red);
}

.bg-black {
  background: black;
}

.text-white {
  color: white;
}

/* containers */

.container-xs {
  max-width: 540px !important;
}

.bottom-border {
  border-bottom: var(--red) 5px solid;
}

/* buttons */

.btn-primary {
  color: #fff;
  background-color: var(--red);
  border-color: var(--red);
}

/* footer */

.footer p {
  font-size: 14px;
}
